
.goods-pwd {
  overflow: hidden;
  clear: both;
}
.goods-pwd .goods-pwd-content {
  width: 308px;
  height: 162px;
  border: 1px solid #ccc;
  margin: 144px auto 40px;
  border-radius: 10px;
}
.goods-pwd .goods-pwd-content .txt {
  line-height: 50px;
  font-size: 18px;
  text-align: center;
  color: #000;
  border-bottom: 1px solid #ccc;
}
.goods-pwd .goods-pwd-content .txt2 {
  text-align: center;
  line-height: 50px;
  font-size: 16px;
}
.goods-pwd .goods-pwd-content .input-list {
  margin: 0 auto;
  width: 279px;
  clear: both;
}
.goods-pwd .goods-pwd-content .input-list input {
  float: left;
  border: 1px solid #ccc;
  width: 30px;
  height: 30px;
  display: inline-block;
  margin-right: -1px;
  text-align: center;
  font-size: 16px;
}
.wechat-btn {
  margin: 30px 30px 60px;
}



