
.goods-pwd {
  overflow: hidden;
  clear: both;
}
.goods-pwd .tips {
  text-align: left;
  width: 80%;
  margin: 100px auto 0;
  font-size: 16px;
}
.goods-pwd .goods-pwd-content {
  width: 308px;
  height: 130px;
  border: 1px solid #ccc;
  margin: 40px auto 0;
  border-radius: 10px;
}
.goods-pwd .goods-pwd-content .txt {
  line-height: 50px;
  font-size: 18px;
  text-align: center;
  color: #000;
  border-bottom: 1px solid #ccc;
}
.goods-pwd .goods-pwd-content .input-list {
  margin: 20px auto 0;
  width: 279px;
  clear: both;
}
.goods-pwd .goods-pwd-content .input-list input {
  float: left;
  border: 1px solid #ccc;
  width: 30px;
  height: 30px;
  display: inline-block;
  margin-right: -1px;
  text-align: center;
  font-size: 16px;
}
.goods-pwd .btn {
  width: 168px;
  height: 35px;
  line-height: 35px;
  display: block;
  margin: 20px auto 0;
  text-align: center;
  font-size: 18px;
  color: #df4b24;
  border: 1px solid #df4b24;
  border-radius: 5px;
}
.goods-pwd .btn-box {
  text-align: center;
  padding: 40px 0;
}
.goods-pwd .btn-box span {
  font-size: 16px;
  color: #0093d3;
}
.wechat-btn {
  margin: 30px 30px 60px;
}



