
.members {
  position: relative;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  padding: 10 11px;
}
.members .img-box {
  overflow: hidden;
  clear: both;
  padding: 22px 0 10px;
}
.members .img-box .img {
  width: 80px;
  height: 80px;
  display: block;
  margin: 0 auto;
}
.members .img-box .name {
  text-align: center;
  line-height: 50px;
}
.members .txt {
  padding: 18px 11px 0;
}
.members .address .weui-cell__ft {
  width: 80%;
}
.members .btn-box {
  text-align: center;
  padding: 40px 0;
}
.members .btn-box span {
  font-size: 16px;
  color: #0093d3;
}



