
.pay-sucess {
  padding: 80px 11px;
}
.pay-sucess .img {
  width: 60px;
  height: auto;
  display: block;
  margin: 0 auto;
}
.pay-sucess .sucess-txt {
  text-align: center;
  line-height: 40px;
  font-size: 20px;
}
.pay-sucess .price {
  padding: 64px 0 0;
  text-align: center;
  font-size: 18px;
}
.pay-sucess .price span {
  font-size: 28px;
}
.pay-sucess .txt {
  padding: 40px 0 0;
  text-align: center;
  font-size: 16px;
  color: #c1c1c1;
}



