
.goods-sucess {
  padding: 80px 11px;
}
.goods-sucess .img {
  width: 60px;
  height: auto;
  display: block;
  margin: 0 auto;
}
.goods-sucess .sucess-txt {
  text-align: center;
  line-height: 40px;
  font-size: 20px;
}
.goods-sucess .txt {
  display: block;
  width: 80%;
  padding: 40px 0 0;
  text-align: center;
  font-size: 16px;
  color: #666;
  margin: 0 auto;
}
.goods-sucess .btn {
  width: 168px;
  height: 35px;
  line-height: 35px;
  display: block;
  margin: 130px auto 0;
  text-align: center;
  font-size: 18px;
  color: #00923f;
  border: 1px solid #00923f;
  border-radius: 5px;
}



