
.vux-close {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  color: #999;
  width: 24px;
  height: 24px;
}
.vux-close:before,
.vux-close:after {
  content: '';
  position: absolute;
  left: 0;
  top: 11px;
  width: 24px;
  height: 1px;
  background-color: currentColor;
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}
.vux-close:after {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.goods-main {
  background: #fff;
}
.goods-main .buy-btn {
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 1000;
  width: 100%;
  height: 66px;
  font-size: 18px;
  color: #fff;
  line-height: 66px;
  text-align: center;
  background: #df4b24;
}
.goods-main .goods-info {
  margin: 4px 22px;
}
.goods-main .goods-info .h3-title {
  position: relative;
  width: 100%;
  font-size: 18px;
  color: #000;
  line-height: 43px;
  font-weight: normal;
}
.goods-main .goods-info .h3-title:after {
  content: " ";
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  height: 1px;
  border-bottom: 1px solid #ccc;
  color: #ccc;
  -webkit-transform-origin: 0 100%;
          transform-origin: 0 100%;
  -webkit-transform: scaleY(0.5);
          transform: scaleY(0.5);
}
.goods-main .goods-info .tips1 {
  font-size: 16px;
  color: #df4b24;
  padding: 15px 0;
}
.goods-main .goods-info .price-box {
  position: relative;
}
.goods-main .goods-info .price-box .yen {
  vertical-align: middle;
  color: #e63c0f;
  font-size: 18px;
  font-family: Arial;
  -webkit-font-smoothing: antialiased;
}
.goods-main .goods-info .price-box .price {
  vertical-align: middle;
  font-size: 30px;
  color: #e63c0f;
  font-weight: bolder;
  font-family: Arial;
  -webkit-font-smoothing: antialiased;
  font-weight: normal;
}
.goods-main .address-box {
  position: relative;
  font-size: 16px;
  padding: 10px 0 15px;
  color: #444;
}
.goods-main .address-box:after {
  content: " ";
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  height: 1px;
  border-bottom: 1px solid #ccc;
  color: #ccc;
  -webkit-transform-origin: 0 100%;
          transform-origin: 0 100%;
  -webkit-transform: scaleY(0.5);
          transform: scaleY(0.5);
}
.goods-main .tips2 {
  font-size: 13px;
  color: #c1c1c1;
  line-height: 20px;
  padding: 10px 0 0;
}
.buy-dialog {
  position: absolute;
  bottom: 66px;
  left: 0;
  width: 100%;
  height: auto;
  background: #fff;
}
.buy-dialog .weui-mask {
  position: fixed;
  z-index: 1000;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.6);
}
.buy-dialog .buy-dialog-main {
  position: relative;
  z-index: 1001;
  left: 0;
  bottom: 0;
  background: #fff;
  padding: 10px 0;
}
.buy-dialog .buy-dialog-main .close-btn {
  position: absolute;
  z-index: 2500;
  right: 10px;
  top: 10px;
  border: 1px solid #ccc;
  border-radius: 30px;
}
.buy-dialog .buy-dialog-main .goods-box {
  position: relative;
  margin: 10px;
  overflow: hidden;
  padding: 10px 0 10px;
}
.buy-dialog .buy-dialog-main .goods-box:after {
  content: " ";
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  height: 1px;
  border-bottom: 1px solid #ccc;
  color: #ccc;
  -webkit-transform-origin: 0 100%;
          transform-origin: 0 100%;
  -webkit-transform: scaleY(0.5);
          transform: scaleY(0.5);
}
.buy-dialog .buy-dialog-main .goods-box .img {
  float: left;
}
.buy-dialog .buy-dialog-main .goods-box .img img {
  width: 80px;
  height: 80px;
  border: 1px solid #f1f1f1;
}
.buy-dialog .buy-dialog-main .goods-box .img-t {
  position: relative;
  top: 5px;
  font-size: 16px;
  color: #000;
  margin-left: 1rem;
}
.buy-dialog .buy-dialog-main .h4-title {
@include sc(0.6rem, #555);
  font-weight: normal;
}
.buy-dialog .buy-dialog-main .goods-standard,
.buy-dialog .buy-dialog-main .goods-num,
.buy-dialog .buy-dialog-main .goods-total {
  position: relative;
  margin: 10px;
}
.buy-dialog .buy-dialog-main .goods-standard:after,
.buy-dialog .buy-dialog-main .goods-num:after,
.buy-dialog .buy-dialog-main .goods-total:after {
  content: " ";
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  height: 1px;
  border-bottom: 1px solid #ccc;
  color: #ccc;
  -webkit-transform-origin: 0 100%;
          transform-origin: 0 100%;
  -webkit-transform: scaleY(0.5);
          transform: scaleY(0.5);
}
.buy-dialog .buy-dialog-main .btn-list {
  padding: 15px 0 20px;
}
.buy-dialog .buy-dialog-main .btn-list .weui-btn_default {
  background: #fff;
  font-size: 14px;
}
.buy-dialog .buy-dialog-main .btn-list .active {
  color: #e63c0f;
}
.buy-dialog .buy-dialog-main .btn-list .weui-btn.active:after {
  border-color: #e63c0f;
}
.buy-dialog .buy-dialog-main .goods-num .weui-cells {
  margin: 0;
}
.buy-dialog .buy-dialog-main .goods-num .weui-cells .weui-cell {
  padding-left: 0;
  padding-bottom: 20px;
}
.buy-dialog .buy-dialog-main .goods-num .weui-cells:before {
  border-top: 0;
}
.buy-dialog .buy-dialog-main .goods-total:after {
  content: " ";
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  height: 1px;
  border-bottom: 1px solid #fff;
  color: #fff;
  -webkit-transform-origin: 0 100%;
          transform-origin: 0 100%;
  -webkit-transform: scaleY(0.5);
          transform: scaleY(0.5);
}
/* 开始过渡阶段,动画出去阶段 */
.fade-enter-active,
.fade-leave-active {
  transition: all 0.5s ease-out;
}
/* 进入开始 */
.fade-enter {
  -webkit-transform: translateY(1000px);
          transform: translateY(1000px);
  opacity: 0;
}
/* 出去终点 */
.fade-leave-active {
  -webkit-transform: translateY(500px);
          transform: translateY(500px);
  opacity: 0;
}
.vux-flexbox-item button.weui-btn,
.vux-flexbox-item input.weui-btn {
  width: 91%;
}



