
.img-box {
  overflow: hidden;
  position: relative;
  margin-top: 60px;
}
.img-box img {
  display: block;
  margin: 0 auto;
}
.img-box .img1 {
  width: 200px;
  heigt: 200px;
  position: relative;
  top: 0;
  left: 0;
}
.img-box .img2 {
  width: 160px;
  heigt: 160px;
  position: relative;
  top: -40px;
  left: 0;
}
.img-box .img3 {
  width: 80%;
  height: 1px;
  position: relative;
  top: 0;
  left: 0;
  margin-bottom: 1.5rem;
}
.txt {
  width: 65%;
  display: block;
  margin: 0 auto;
  color: #999;
}
.btn-box {
  width: 60%;
  margin: 1.5rem auto;
}
.btn-box .weui-btn_plain-default {
  color: #df4924;
  border: 1px solid #df4924;
}
.into-members {
  margin-top: 60px;
  text-align: center;
  color: #0098cc;
}



